import React from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import { CatalogPage } from './pages/CatalogPage';
import { AppDetailPage } from './pages/AppDetailPage';
import { AdminLoginPage } from './pages/AdminLoginPage';
import { AdminDashboardPage } from './pages/AdminDashboardPage';
import ProtectedRoute from './components/ProtectedRoute';

function App() {
  return (
    <Router>
      <div className="min-h-screen bg-telegram-light">
        <header className="bg-telegram-background border-b border-telegram-border fixed top-0 left-0 right-0 z-10">
          <div className="max-w-[480px] mx-auto px-4 py-3">
            <h1 className="text-lg font-medium text-telegram-text">
              ITOQ Space
            </h1>
          </div>
        </header>
        <main className="max-w-[480px] mx-auto px-4 py-8 pt-16">
          <Routes>
            <Route path="/" element={<CatalogPage />} />
            <Route path="/app/:id" element={<AppDetailPage />} />
            <Route path="/admin/login" element={<AdminLoginPage />} />
            <Route
              path="/admin"
              element={
                <ProtectedRoute>
                  <AdminDashboardPage />
                </ProtectedRoute>
              }
            />
          </Routes>
        </main>
      </div>
    </Router>
  );
}

export default App;
